        :visible.sync="dialogFormVisible"
        top="5%"
        v-if="dialogFormVisible"
        :close-on-click-modal="false"
        destroy-on-close
      >
        <CategoryForm
          :categoryId="id"
          @cancel="closeDialog"
          @submit="reloadList"
        ></CategoryForm>
      </el-dialog>
