<script>
import PageHeader from "components/common/menu/PageHeader";
import CategoryForm from "@/views/master/CategoryForm";
import {
  getCategoryDataAll,
  deleteCategory,
  searchCategoryData,
} from "api/category";
import { Message } from "element-ui";

export default {
  name: "CategoryList",
  components: {
    PageHeader,
    CategoryForm,
  },
  data() {
    return {
      ruleForm: {
        category_name: "",
        sorting: "",
      },
      category_name: "",
      sorting: "",
      id: "",
      dialogFormVisible: false,
      loading: false,
      tableData: [],
      search: "",
    };
  },
  created() {
    this.getCategoryDataAll();
  },
  methods: {
    getCategoryDataAll() {
      this.loading = true;
      getCategoryDataAll()
        .then((res) => {
          if (res.data.status === 1) {
            this.tableData = res.data.data;
            //将父商品分类ID转换为商品分类名
            // for (let idItem of this.tableData) {
            //   for (let parentIdItem of this.tableData) {
            //     if (idItem.parent_id == parentIdItem.id) {
            //       idItem.parent_id = parentIdItem.category_name
            //     }
            //   }
            // }
            this.loading = false;
          } else {
            this.tableData = [];
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    searchCategoryData() {
      let values = {};
      values["category_name"] = this.ruleForm.category_name;
      values["sorting"] = this.ruleForm.sorting;
      searchCategoryData(values)
        .then((res) => {
          console.log(res);
          this.tableData = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchCategoryData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.id = row.id;
      this.dialogFormVisible = true;
    },
    handleDelete(index, row) {
      // this.loading = true
      let values = {};
      values["id"] = row.id;
      deleteCategory(values)
        .then((res) => {
          if (res.status === 200) {
            this.getCategoryDataAll();
            Message({
              showClose: true,
              message: res.data.message,
              type: "success",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    closeDialog() {
      this.dialogFormVisible = false;
    },
    reloadList() {
      this.dialogFormVisible = false;
      this.getCategoryDataAll();
    },
    createBtn() {
      this.id = "";
      this.dialogFormVisible = true;
      console.log(this.dialogFormVisible);
    },
  },
};
</script>

